body {
    box-sizing: border-box;
    cursor: url("../imeges/cursor-wing.png"), pointer;
    background-image: url("../imeges/fon.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    margin: 0;
}

.board {
    width: 100vw;
    height: 100vh;
}

.btn {
    position: absolute;
    left: 50%;
    top: 50%;
    transition: 0.5s;

    width: 120px;
    height: 120px;
    font-size: 25px;
    border-radius: 10px;
    border: none;
    outline: none;
    text-transform: uppercase;
    font-weight: bold;

    /* background-image: url(./img/hadMax.png); */
    background-image: url(../imeges/frog4.png);

    background-repeat: no-repeat;
    background-size: contain;
}

#rocket {
    position: fixed;
    font-size: 42px;
    left: 48%;
    color: green;
}

.bounce-top {
    -webkit-animation: bounce-top 0.9s infinite both;
    animation: bounce-top 0.9s infinite both;
}

.rotate-90-cw {
    -webkit-animation: rotate-90-cw 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
    animation: rotate-90-cw 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}
