.btn {
    display: inline-block;
    margin: 6px;
    font-size: inherit;
    line-height: 1.42;
    padding: 0.8em 1.6em;
    font-weight: normal;
    border-width: 0;
    border-style: solid;
    background: transparent;
    border-radius: 0.2666666667em;
    cursor: pointer;
    font-family: "Booster Next FY", "Avenir Next", Avenir, sans-serif;
    user-select: none;
    vertical-align: bottom;
    transition: box-shadow 0.2s, transform 0.2s, color 0.2s;
}

.btn-primary {
    box-shadow: 0 3px 0 0 #b3004a, 0 4px 4px -1px rgb(0 0 0 / 60%), 0 4px 6px 1px rgb(0 0 0 / 30%),
        0 1px 2px 1px rgb(0 0 0 / 0%) inset, 0 18px 32px -2px rgb(255 255 255 / 10%) inset;
    background-color: #ff006a;
    background-image: linear-gradient(-45deg, #ff006a, #ff00aa);
    color: #e6eaef;

    &:hover {
        box-shadow: 0 3px 0 0 #b3004a, 0 6px 4px -1px rgb(0 0 0 / 30%), 0 4px 16px rgb(0 0 0 / 50%),
            0 1px 2px 1px rgb(0 0 0 / 0%) inset, 0 18px 32px -2px rgb(255 255 255 / 14%) inset;
    }
}

.btn-secondary-active {
    box-shadow: 0 3px 0 0 #367f14, 0 4px 4px -1px rgb(0 0 0 / 60%), 0 4px 6px 1px rgb(0 0 0 / 30%),
        0 1px 2px 1px rgb(0 0 0 / 0%) inset, 0 18px 32px -2px rgb(255 255 255 / 10%) inset;
    background-color: #52c11f;
    background-image: linear-gradient(-45deg, #52c11f, #7ccc14);
    color: #e6eaef;
}
.btn-primary-active {
    box-shadow: 0 3px 0 0 #367f14, 0 4px 4px -1px rgb(0 0 0 / 60%), 0 4px 6px 1px rgb(0 0 0 / 30%),
        0 1px 2px 1px rgb(0 0 0 / 0%) inset, 0 18px 32px -2px rgb(255 255 255 / 10%) inset;
    background-color: #52c11f;
    background-image: linear-gradient(-45deg, #52c11f, #7ccc14);
    color: #e6eaef;
}
.btn-active {
    transform: translateY(3px);
    display: inline-block;
    margin: 6px;
    font-size: inherit;
    line-height: 1.42;
    padding: 0.8em 1.6em;
    font-weight: normal;
    border-width: 0;
    border-style: solid;
    border-radius: 0.2666666667em;
    cursor: pointer;
    font-family: "Booster Next FY", "Avenir Next", Avenir, sans-serif;
    user-select: none;
    vertical-align: bottom;
    transition: box-shadow 0.2s, transform 0.2s, color 0.2s;
}
